html, body, #root {
  height: 100%;
}

body {
  font-family: 'Graphik', sans-serif;
  letter-spacing: -1px;

  width:100%;
	height:100vh;
}

.wrap-app-grid {
  height: 100%;
  display: grid;
  grid-template-rows: 50px 1fr 50px;
  position: relative;
}

.wrap-header {
  position: fixed;
  top: 0;
  display: flex;
  justify-content: space-between;
  width: calc(100% - 40px);
  padding: 0 20px;
  align-items: center;
  z-index: 99;
  backdrop-filter: blur(2px);
}

.wrap-header.scrolled {
  padding: 5px 20px;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.1);
  transition: 0.3s ease;
}

.logo-txt {
  font-size: 16px;
  font-weight: 600;
}

.wrap-header-li {
  display: flex;
}

.wrap-header-li li {
  margin-left: 14px;
  list-style: none;
}

.wrap-main-txt {
  padding: 30px;
}

.copy-txt {
  font-size: 14px;
  opacity: 0.6;
  margin-right: 20px;
  text-align: right;
}

.hello-txt {
  font-size: 28px;
  font-weight: 600;
  margin: 2px 0;
}

.main-txt {
  font-size: 18px;
  font-weight: 500;
  margin: 30px 0 10px 0;
  line-height: 30px;
}

.wrap-gall {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 20px;
  grid-column-gap: 20px;
  margin: 20px;
}


.wrap-gallary {
  border: 1px solid #eee;
  padding: 20px;
  margin: 2px;
  position: relative;
}

.wrap-gallary h3 {
  margin: 0;
  display: inline-block;
}

.wrap-gallary .sub-title {
  margin: 2px 0 0 0;
  color: rgba(0, 0, 0, 0.5);
  display: inline-block;
  margin-left: 10px;
}

.wrap-gallary .wrok-type {
  margin: 5px 0 0 0;
  display: inline-block;
  color: rgba(0, 0, 0, 0.6);
}

.wrap-gallary .work-skill {
  margin: 5px 0 0 10px;
  display: inline-block;
  color: rgba(0, 0, 0, 0.6);
  font-weight: 700;
}

.wrap-gallary .sub-work {
  margin: 0 0 10px 0;
}

.wrap-gal-img {
  height: 300px;
  background: #eee;
}

.wrap-gal-img image {
  width: 100%;
  height: 100%;
  display: inline-block;
}

.wrap-gal-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-color: #e0e0e0;
}

footer {
  border-top: 1px solid #eee;
}

.open > div {
  pointer-events: none;
}

.open {
  border: 3px solid rgba(0, 0, 0, 0);
  background-color: rgba(0, 0, 0, 0);
  padding: 0;
}

.open:hover {
  cursor: pointer;
  border: 3px solid rgba(0, 0, 0, 0.3);
  /* background-color: rgba(0, 0, 0, 0.1); */
  transition: 0.5s;
  box-sizing:content-box;
  box-shadow: inset 0px 0px 0px 3px rgba(0, 0, 0, 0.3);
}


.wrap_img {
  width: 100%;
  align-items: center;
}

/*Modal CSS*/
.modal {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 99;
  background-color: rgba(0, 0, 0, 0.8);
}
.modal button {
  outline: none;
  cursor: pointer;
  border: 0;
}
.modal > section {
  width: 90%;
  max-width: 1050px;
  height: 80%;
  margin: 0 auto;
  border-radius: 0.3rem;
  background-color: #fff;
  /* 팝업이 열릴때 스르륵 열리는 효과 */
  animation: modal-show 0.3s;
  overflow-x: hidden;
  overflow-y: auto;
}
.modal > section > header {
  position: relative;
  padding: 16px 64px 16px 16px;
  background-color: #fff;
  font-weight: 700;
}
.modal > section > header button {
  position: absolute;
  top: 10px;
  right: 15px;
  width: 30px;
  font-size: 30px;
  font-weight: 700;
  text-align: center;
  color: #999;
  background-color: transparent;
  font-family: monospace;
}
.modal > section > main {
  padding: 0px;
  border-bottom: 1px solid #dee2e6;
  border-top: 1px solid #dee2e6;
}
.modal > section > footer {
  padding: 12px 16px;
  text-align: right;
}
.modal > section > footer button {
  padding: 6px 20px;
  color: #fff;
  background-color: #424242;
  border-radius: 5px;
  font-size: 13px;
  font-weight: 700;
}
.modal.openModal {
  display: flex;
  align-items: center;
  /* 팝업이 열릴때 스르륵 열리는 효과 */
  animation: modal-bg-show 0.3s;
}
@keyframes modal-show {
  from {
    opacity: 0;
    margin-top: -50px;
  }
  to {
    opacity: 1;
    margin-top: 0;
  }
}
@keyframes modal-bg-show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/*Modal CSS*/

/*Modal Scroll CSS*/
.modal > section::-webkit-scrollbar {
  width: 10px;
}

.modal > section::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-clip: padding-box;
  border: 2px solid transparent;
}

.modal > section::-webkit-scrollbar-track {
  background-color: rgba(0, 0, 0, 0);
  border-radius: 10px;
  box-shadow: inset 0px 0px 5px white;
}
/*Modal Scroll CSS*/

.wrap-loading {
  display: grid;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  text-align: center;
  align-items: center;
  color: white;
  font-size: 20px;
}